import React, { Component } from 'react';
import {Button, Menu} from 'semantic-ui-react';

export default class Header extends Component {

    render() {

        return (
            <Menu color="blue" widths={2} size="massive" classname="min-width-480" borderless>
                <Menu.Item />
                <Menu.Item >
                    <Button
                        style={{marginLeft: '75%'}}
                        icon='setting'
                        color='blue'
                        size="small"
                        onClick={ () => this.setState({settings: true})}
                    />
                </Menu.Item>
            </Menu>
        )
    }
}