import React, { Component } from 'react';
import {Button, Dimmer, Divider, Grid, Header, Image, Loader, Menu, Segment} from "semantic-ui-react";

class AppHome extends Component {

    state = {
        data: null,
        activeItem: 'upload',
        uploadStatus: 'none'
    };

    componentDidMount() {
        // Call our fetch function below once the component mounts
        this.callBackendAPI()
            .then(res => this.setState({ data: res.express }))
            .catch(err => console.log(err));
    }

    callBackendAPI = async () => {
        const response = await fetch('/express_backend');
        const body = await response.json();

        if (response.status !== 200) {
            throw Error(body.message)
        }
        return body;
    };

    handleItemClick = (e, { value }) => this.setState({ activeItem: value })

    render() {

        const { activeItem, uploadStatus } = this.state;

        return (
            <Grid columns='equal'>
                <Grid.Column width={4}/>

                <Grid.Column width={8}>
                    <Segment basic>
                        <div>
                            <Divider horizontal>
                                <Header className="c9-text-big" style={{color: 'black'}}>Upload Method</Header>
                            </Divider>

                            <Menu attached='top' tabular>
                                <Menu.Item
                                    name='Upload Document'
                                    value='upload'
                                    active={activeItem === 'upload'}
                                    onClick={this.handleItemClick}
                                />
                                <Menu.Item
                                    name='Enter Text'
                                    value='text'
                                    active={activeItem === 'text'}
                                    onClick={this.handleItemClick}
                                />
                            </Menu>

                            <Segment attached='bottom'>
                                <Image src='https://react.semantic-ui.com/images/wireframe/paragraph.png' />
                                <Button
                                    label='Upload'
                                    onClick={ () => this.setState({uploadStatus: 'work'})}
                                />
                            </Segment>

                        </div>
                    </Segment>

                    {uploadStatus === 'work' ?
                        <Segment basic>
                            <Divider horizontal>
                                <Header className="c9-text-big" style={{color: 'black'}}> Play </Header>
                            </Divider>

                            <Segment>
                                <Dimmer active inverted>
                                    <Loader inverted>Loading</Loader>
                                </Dimmer>

                                <Image src='https://react.semantic-ui.com/images/wireframe/short-paragraph.png' />
                            </Segment>
                        </Segment>
                        : null
                    }

                    {uploadStatus === 'done' ?
                        <Segment basic>
                            <Divider horizontal>
                                <Header className="c9-text-big" style={{color: 'black'}}> Play </Header>
                            </Divider>

                            <Segment attached='bottom'>
                                <Image src='https://react.semantic-ui.com/images/wireframe/paragraph.png' />
                            </Segment>
                        </Segment>
                        : null
                    }

                </Grid.Column>

                <Grid.Column width={4} />
            </Grid>
        );
    }
}

export default AppHome;